import React from 'react'
import SVG from "./SVG"
import "./PedirOrcamentoDispositivo.css"

export default function PedirOrcamentoDispositivo(props) {

    function enviarCrisp() {
        window.$crisp.push(["do", "chat:open"])

        switch(props.dispositivo) {
            case("Telemóvel"):
                window.$crisp.push(["set", "message:text", [`Olá ! Tenho um telemóvel que precisa de arranjo.`]])
                break
            case("Computador"):
                window.$crisp.push(["set", "message:text", [`Olá ! Tenho um computador que precisa de arranjo.`]])
                break
            case("Tablet"):
                window.$crisp.push(["set", "message:text", [`Olá ! Tenho um tablet que precisa de arranjo.`]])
                break
            default: 
                window.$crisp.push(["set", "message:text", [`Olá ! Tenho um dispositivo que precisa de arranjo.`]])

        }
    }

    return (
        <div className="escolhaDispositivo" onClick={enviarCrisp}>
            <SVG svgName={props.svgName} />
            <span>{props.dispositivo}</span>
        </div>
    )
}
