import React, { useEffect, useState } from 'react'
import "./Feature.css"

export default function Feature(props) {

    function debounce(fn, ms) {
        let timer
        return _ => {
          clearTimeout(timer)
          timer = setTimeout(_ => {
            timer = null
            fn.apply(this, arguments)
          }, ms)
        };
      }
    
    const [dimensions, setDimensions] = useState({ 
        height: window.innerHeight,
        width: window.innerWidth
    })
        
    useEffect(() => {
        const debouncedHandleResize = debounce(function handleResize() {
            setDimensions({
              height: window.innerHeight,
              width: window.innerWidth
            })
          }, 1000)
          
        window.addEventListener('resize', debouncedHandleResize)

        return _ => {
            window.removeEventListener('resize', debouncedHandleResize)
        }
    })

        return (
            <div className={`feature ${props.reversa}`}>
                {dimensions.width>=950 && <><img className={`featureIMG ${props.reversa}IMG`} src={props.imgFeature} alt={props.imgAltFeature}/>
                <div className="textoFeature">
                    <h3 className="h3Azul">{props.h3Azul}</h3>
                    <h2 className="tituloFeature">{props.tituloFeature}</h2>
                    <p className="conteudoFeature">{props.conteudoFeature}</p>
                </div></>}
                {dimensions.width<950 && <>
                <div className="textoFeature">
                    <h3 className="h3Azul">{props.h3Azul}</h3>
                    <h2 className="tituloFeature">{props.tituloFeature}</h2>
                    <img className={`featureIMG ${props.reversa}IMG`} src={props.imgFeature} alt={props.imgAltFeature}/>
                    <p className="conteudoFeature">{props.conteudoFeature}</p>
                </div></>}
            </div>
        )
}
