import React from 'react'
import FAQItem from "./FAQItem"
import "./FAQS.css"


export default function FAQS() {

        return (
            <div className="FAQScontainer">
                <h3>FAQ's</h3>
                <div className="accordion">
                    <FAQItem 
                        FAQQuestion="Onde posso encontrar a loja?"
                        FAQAnswer="Estamos nas Caldas da Rainha, de momento não temos loja física o que nos possibilita proporcionar um preço mais competitivo no mercado, rapidez no serviço e personalização do serviço prestado." />
                    <FAQItem
                        FAQQuestion="Como posso enviar o meu equipamento para reparação?"
                        FAQAnswer="A receção/entrega do teu equipamento pode ser efetuada em mão na zona das Caldas da Rainha, se assim não o for possível, poderás enviar por correio, mas aconselhamos sempre a acondicionar bem a encomenda para não haver danos extra no transporte do equipamento. Quaisquer atrasos na receção/entrega do equipamento são da responsabilidade das empresas de transporte." />
                    <FAQItem
                        FAQQuestion="Quanto tempo demora uma reparação?"
                        FAQAnswer="As reparações têm um período máximo de 24 horas, mas a grande parte das reparações são feitas em menos de 2 horas, o que possibilita a entrega no próprio dia!" />
                    <FAQItem
                        FAQQuestion="Que tipos de serviços são feitos pela Wi Fix It?"
                        FAQAnswer="Fazemos instalação de Software em computadores, limpezas internas e externas (Hardware ou Software), reparação de teclados de portáteis, upgrade de Software e Hardware, reparação geral de telemóveis e todo o tipo de serviços relacionados com informática. Qualquer informação adicional ou duvida não hesites em contactar!" />
                    <FAQItem
                        FAQQuestion="Em que situação sou reembolsado?"
                        FAQAnswer="Se o aparelho deixar de funcionar ou tenha algum erro de Software dentro dos primeiros 15 dias após a intervenção, poderás ser reembolsado ou pedir nova reparação. No caso de novos danos físicos esta norma não se aplica e terás, se assim o desejares, de pagar novamente o serviço." />
                </div>
            </div>
        )
    
}
