import React from 'react'
import ComponenteFooter from "./ComponenteFooter"
import RedesSociaisFooter from "./RedesSociaisFooter"

import LogotipoGfleck from "../assets/gfleck.png"
import SimboloWifixit from "../assets/simbolo-wifixit.png"

import "./Footer.css"


export default function Footer() {
    return (
        <div className="footer">
            <div className="footerCima">
                <img src={SimboloWifixit} alt="Favicon da WiFixIt" />
                <ComponenteFooter 
                    titulo="Serviço"
                    links={[
                        {hrefLink: "/", nomeLink: "Pedir orçamento"}
                        ]} />
                <ComponenteFooter 
                    titulo="Suporte"
                    links={[
                        {hrefLink: "politica-de-privacidade", nomeLink: "Política de Privacidade"},
                        {hrefLink: "termos-e-condicoes", nomeLink: "Termos e Condições"}
                        ]} />
                <RedesSociaisFooter />
            </div>

            <div className="footerBaixo">
                <span>&copy; 2020 WiFixIt - Todos os direitos reservados</span>

                <a href="https://g-fleck.com" target="_blank" rel="noreferrer">
                    <img src={LogotipoGfleck} alt="Logotipo da g-fleck"/>
                </a>
            </div>
        </div>
    )
}
