import React from 'react'
import PedirOrcamentoDispositivo from "./PedirOrcamentoDispositivo.js"
import "./PedirOrcamentoOverlay.css"

export default function PedirOrcamentoOverlay(props) {

    return (
        <div className="contentBoxOverlay" style={{display: props.showOverlay===true ? "flex" : "none" }}>

            <div className="pedirOrcamentoOverlay">
                <p>Qual o dispositivo que precisa de arranjo ?</p>
                <div className="variasEscolhas">
                    <PedirOrcamentoDispositivo
                        dispositivo="Telemóvel"
                        svgName="telemovel" />
                    <PedirOrcamentoDispositivo 
                        dispositivo="Computador"
                        svgName="computador" />
                    <PedirOrcamentoDispositivo 
                        dispositivo="Tablet"
                        svgName="tablet" />
                </div>
            </div>

        </div>
    )
}
