import React, {useState, useEffect} from 'react'
import "./BarraAzul.css"

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faWhatsapp } from '@fortawesome/free-brands-svg-icons'
import { faInstagram } from '@fortawesome/free-brands-svg-icons'

export default function BarraAzul() {
    function debounce(fn, ms) {
        let timer
        return _ => {
          clearTimeout(timer)
          timer = setTimeout(_ => {
            timer = null
            fn.apply(this, arguments)
          }, ms)
        };
      }
    
    const [dimensions, setDimensions] = useState({ 
        height: window.innerHeight,
        width: window.innerWidth
    })
        
    useEffect(() => {
        const debouncedHandleResize = debounce(function handleResize() {
            setDimensions({
              height: window.innerHeight,
              width: window.innerWidth
            })
          }, 1000)
          
        window.addEventListener('resize', debouncedHandleResize)

        return _ => {
            window.removeEventListener('resize', debouncedHandleResize)
        }
    })

    return (
        <div className="barraAzul">
            <div className="textoBarraAzul">
                <h3>Dúvidas ou serviços não referidos</h3>
                <p>Qualquer serviço não mencionado acima contacta-nos para um esclarecimento mais personalizado!</p>
            </div>
            <div className="divFontAwesome">
                <a href="https://www.instagram.com/wi_fix_it/"
                    target="_blank" rel="noreferrer">
                    {dimensions.width < 900 && <FontAwesomeIcon icon={faInstagram} color="#59BCEE" size="3x" />}
                    {dimensions.width >= 900 && <FontAwesomeIcon icon={faInstagram} color="#59BCEE" size="4x" />}
                </a>
                <a href="https://wa.me/+351934659645?text=Ol%C3%A1+%21+Estou+interessado+nos+servi%C3%A7os+da+WiFixIt"
                    target="_blank" rel="noreferrer">
                    {dimensions.width < 900 && <FontAwesomeIcon icon={faWhatsapp} color="#59BCEE" size="3x" />}
                    {dimensions.width >= 900 && <FontAwesomeIcon icon={faWhatsapp} color="#59BCEE" size="4x" />}
                </a>
            </div>
        </div>
    )
}
