import { useState, useEffect } from "react"
import { BrowserRouter } from "react-router-dom"
import Routes from "./Routes"


import Navbar from "./components/Navbar"
import Footer from "./components/Footer"
import PedirOrcamentoOverlay from "./components/PedirOrcamentoOverlay"
import Crisp from "./components/Crisp"

import './Media.css'

function App() {
  const [showOverlay, setShowOverlay] = useState(false)

  useEffect(() => {
    const botoesOrcamento = document.querySelectorAll(".botaoOrcamento")
    
    const abrirOverlay = (event) => {
      setShowOverlay(true)
    }
    
    botoesOrcamento.forEach(botao => botao.addEventListener('click', abrirOverlay))
    return () => {
      botoesOrcamento.forEach(botao => botao.removeEventListener('click', abrirOverlay))
    }
  }, []);
  
  
  useEffect(() => {
    const foraOverlay = document.querySelector(".contentBoxOverlay")
    const dentroOverlay = document.querySelector(".pedirOrcamentoOverlay")
    const cadaDispositivo = document.querySelectorAll(".escolhaDispositivo")

    const fecharOverlay = (e) => { 
      if(e.target === foraOverlay && e.target !== dentroOverlay) setShowOverlay(false) }
  
    const clickDispositivo = (e) => { setShowOverlay(false)}

    cadaDispositivo.forEach(dispositivo => {
      dispositivo.addEventListener("click", clickDispositivo)
    })

    window.addEventListener("click", fecharOverlay) 
    
    return () => {
      window.removeEventListener("click", fecharOverlay)
      cadaDispositivo.forEach(dispositivo => dispositivo.removeEventListener('click', clickDispositivo))
    }
  }, [])
  
  
  return (
    <BrowserRouter>
      <div>
        <Navbar />
        <Routes />
        <Footer />
        <Crisp />
        <PedirOrcamentoOverlay showOverlay={showOverlay} />
      </div>
    </BrowserRouter>
  );
}

export default App;
