import React from 'react'
import LogoTransparente from "./LogoTransparente"
import Feature from "./Feature"
import BarraAzul from "./BarraAzul"
import FAQS from "./FAQS"
import Feature1IMG from "../assets/feature1.png"
import Feature2IMG from "../assets/feature2.png"

export default function Body() {
    return (
        <div>
            <LogoTransparente />
            <Feature
                imgFeature={Feature1IMG}
                imgAltFeature="Reparação rápida, preços baixos, contacto fácil"
                h3Azul="Reparações nas Caldas da Rainha"
                tituloFeature="Arranjamos o teu dispositivo"
                conteudoFeature="O teu telemóvel, computador ou tablet deixou de funcionar? Reparamos o teu dispositivo! Está lento ou partido? Nós damos-te a solução. Na Wi Fix iT somos especialistas na reparação de dispositivos de todas as marcas. Contacta-nos e rapidamente te encontraremos a solução!" />
            <Feature reversa="featureReversa"
                imgFeature={Feature2IMG}
                imgAltFeature="Peças para construção de um desktop"
                h3Azul="Gaming ou Trabalho?"
                tituloFeature="Construímos computadores à medida"
                conteudoFeature="Receias escolher componentes que não são compatíveis? 
                Na Wi Fix iT desenvolvemos o melhor setup possível para a tua futura máquina seja ela de trabalho ou Gaming. Estamos sempre a par das melhores e mais recentes tecnologias na área. Cuidamos de todo o processo, desde a encomenda à montagem, e entregamos um PC pronto a utilizar no seu máximo potencial. Dá-nos o teu budget e guiar-te-emos à melhor opção. Contacta-nos e faz hoje o teu PC de sonho!"/>
            <BarraAzul />
            <FAQS />
        </div>
    )
}

