import React from 'react'

import "./ComponenteFooter.css"

export default function ComponenteFooter(props) {

    let listLinks = ""

    if(props.links) {
        const links = props.links
        listLinks = links.map(objectLink => {
        return (
            <li key={objectLink.nomeLink}>
                <a href={objectLink.hrefLink}>{objectLink.nomeLink}</a>
            </li>
            )
        })
    }
        
    return (
        <div className="componenteFooter">
            <p>{props.titulo}</p> 
            <ul>
                {listLinks}
            </ul>
        </div>
    )
}
